export default {
	data() {
		return {
			isIphoneX: false,
			
			open: false,
			title: '',
			
			mode: 0
		}
	},
	//加载数完成
	mounted: function (){
		this.isIphoneX = getApp().globalData.isIphoneX
	},
	
	methods: {
		//展示
		toOpen: function(propOb){
			this.title = propOb.title||undefined
			this.mode = propOb.mode||undefined
			this.succ = propOb.success;
			this.cancel = propOb.cancel;
			this.open = true;
			
		},
		
		//点击遮罩关闭
		maskClick: function(){
			this.open = false
			this.cancel()
		},
		
		//点击确认
		mySucc: function(){
			this.open = false
			this.succ()
		},
		
		//点击取消
		myCancel: function(){
			this.open = false
			this.cancel()
		}
	}
	
}
